.App {

  
}
.bars{
  position: absolute;
  top:33px;
  left:40px;
  width:36px;
  height:22px;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  z-index: 99999;
  opacity: 0;
  -webkit-transition: opacity 0.8s;
  -o-transition: opacity 0.8s;
  transition: opacity 0.8s;

}
.bars > div{
  width:8px;
  height:100%;
  background-color: #FFFFFF;

}

.header-logo{
  position: absolute;
  right:40px;
  top:20px;
  height: 40px;
  width:80px;
  z-index: 9999;
  -webkit-transition: opacity 0.8s;
  -o-transition: opacity 0.8s;
  transition: opacity 0.8s;
  opacity:0;
}

.footer-logo {
  position: absolute;
  bottom:20px;
  right:40px;
  height: 60px;
  z-index: 9999;
  -webkit-transition: opacity 0.8s;
  -o-transition: opacity 0.8s;
  transition: opacity 0.8s;
  opacity:1;
}

.open .footer-logo{
  opacity:0;
}
.open .header-logo{
  opacity:1;
}
.open .bars{
  opacity:1;
}
.header-logo svg{
  position: absolute; 
  height: 100%; 
  width: 100%; 
  left: 0; 
  top:0;
}

.App-header {
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.st0{fill:#FFFFFF;}
.st1{fill:#FF595A;}



.panels{
  position: absolute;
  width:100vw;
  height:100vh;
  z-index: 4444;
  display: -ms-flexbox;
  display: flex;
}
.panels.mobile{
  -ms-flex-direction: column;
      flex-direction: column;
}
.panels.mobile .panel{
  width:100vw;
  height:33.3333333vh;
}
.panels.mobile .panel .message{
  display:none;
}
.panel{
  width:33.33333333vw;
  color:#FFFFFF;
  
  
  -webkit-transition: width 0.8s, height 0.8s cubic-bezier(0.165, 0.840, 0.440, 1.000);
  
  
  -o-transition: width 0.8s, height 0.8s cubic-bezier(0.165, 0.840, 0.440, 1.000);
  
  
  transition: width 0.8s, height 0.8s cubic-bezier(0.165, 0.840, 0.440, 1.000);
  position: relative;
  overflow: hidden;
}
.panel .content{
  position:absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;
  z-index:2;

}
.panel .message{
  position:absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;
  z-index:3;
  background-position: center;
  background-size: center;
  background-repeat: no-repeat;
  opacity:0;
  -webkit-transition: opacity 0.8s;
  -o-transition: opacity 0.8s;
  transition: opacity 0.8s;
}
.open-design .panel.design .content h2,
.open-projects .panel.projects .content h2,
.open-people .panel.people .content h2{
  top:110px;
}
.open-design .panel.design .message,
.open-projects .panel.projects .message,
.open-people .panel.people .message{
  opacity:1;
}
.panel .content h2{
  font-size: 48px;

  position:absolute;
  top:50%;
  left:40px;
  right:0;
  bottom:0;
  -webkit-transition: top 0.8s cubic-bezier(0.165, 0.840, 0.440, 1.000);
  -o-transition: top 0.8s cubic-bezier(0.165, 0.840, 0.440, 1.000);
  transition: top 0.8s cubic-bezier(0.165, 0.840, 0.440, 1.000);
  margin-top:-30px;

  
}
.panels.mobile .panel .content h2{
  top:70px;
  font-size: 38px;
}
.open-design .panels.mobile .panel.design .content h2,
.open-projects .panels.mobile .panel.projects .content h2,
.open-people .panels.mobile .panel.people .content h2{
  top:110px;
  font-size: 38px;
}
.panel .img{
  position: absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;
  z-index:1;
  background-position: center;
  background-size: cover;
  opacity:0;
  -webkit-transition: opacity 0.8s;
  -o-transition: opacity 0.8s;
  transition: opacity 0.8s;

}

.panel.show{
  width:95vw;
}
.panel.show .img{
  opacity:0.3;
}


.open-design .panel.projects,
.open-design .panel.people,
.open-projects .panel.design,
.open-projects .panel.people,
.open-people .panel.design,
.open-people .panel.projects{
  width:0vw;
}
.open-design .panel.design,
.open-projects .panel.projects,
.open-people .panel.people{
  width:100vw;
}
.open-design .panels.mobile .panel.design,
.open-projects .panels.mobile .panel.projects,
.open-people .panels.mobile .panel.people{
  height:100vh !important;
  width:100vw;
}
.open-design .panels.mobile .panel.projects,
.open-design .panels.mobile .panel.people,
.open-projects .panels.mobile .panel.design,
.open-projects .panels.mobile .panel.people,
.open-people .panels.mobile .panel.design,
.open-people .panels.mobile .panel.projects{
  height:0vh;
  width:100vw;
}
.open-people .panel.people .img,
.open-projects .panel.projects .img,
.open-design .panel.design .img{
  opacity:1;
}


.panel.design{
  background-color: #2e2e2e;
}
.panel.projects{
  background-color: #393939;
}
.panel.people{
  background-color: #434343;
}

.main{
  position: absolute;
  top:100vh;
  left:0;
  right:0;
  overflow: hidden;
  height: 0vh;
}
.main-wrapper{
  padding:40px;
}
.open .main{
  height: 100vh;
}

.row{
  display: -ms-flexbox;
  display: flex;
  margin:0 -20px;

}
.row .col{
  background-color: #CCC;
  min-height:300px;
  margin:20px;
  width:100%;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}




